@charset "utf-8";

html {
    width: 100%;
    height: 100%;
}

h1.condensed-light {
    font-weight: 300;
    margin-bottom: -1.3em;
    @media(max-width: 768px) {
        margin-bottom: -1.2em;
    }
}

#sitelinks img {
    width: auto;
}

.vertical-center {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

.blue {
    color: #0061aa;
}

.bg-image-scepman {
    width: 100%;
    height: auto;
    padding: 50px 0;
    position: relative;
    background: url(https://res.cloudinary.com/glueckkanja/image/upload/v1568114712/backgrounds/background-scepman.jpg) repeat fixed;
  }
  
  .bg-image-scepman:before {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    content: " ";
    position: absolute;
    background: rgba(255,255,255,0.7);
  }  
